.preloader {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #080808;
  color: white;
  -webkit-animation: preloader 1s ease-in-out 6s forwards;
          animation: preloader 1s ease-in-out 6s forwards;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100000000000;
}

@-webkit-keyframes preloader {
  to {
    opacity: 0;
  }
}

@keyframes preloader {
  to {
    opacity: 0;
  }
}

.preloader_content {
  display: flex;
  font-size: 3rem;
  font-family: "Source Code Pro", monospace;
  opacity: 0;
  transition: opacity 0.6s ease-in-out 1s;
}
.preloader_content > div {
  overflow: hidden;
}

.preloader_pixala div {
  -webkit-transform: translateX(-150%);
          transform: translateX(-150%);
  transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1) 4s;
}

.preloader_el {
  -webkit-transform: translateX(200%);
          transform: translateX(200%);
  transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1) 3s;
}

