/* hamburger  */

@media screen and (max-width: 800px) {
  .nav-content {
    display: none;
  }

  .nav-title {
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }

  .burger_nav {
    z-index: 1000000000;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    background: #080808;
    width: 0%;
    transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    overflow: hidden;
  }

  .burger_nav_active {
    width: 50%;
  }

  .hamburger_icon {
    width: 2rem;
    height: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin-top: 1rem;
    position: absolute;
    top: 0;
    right: 5%;
    z-index: 10000000000;
  }
  .hamburger_icon div {
    height: 3px;
    width: 100%;
    background: white;
  }
  .hamburger {
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .hamburger_links {
    color: white;
    width: 100%;
    height: 40%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-transform: uppercase;
    font-size: 1.8rem;
    pointer-events: none;
    cursor: none;
    opacity: 0;
    transition: opacity 0.2s linear;
  }

  .hamburger_links a {
    color: white;
  }
  .hamburger_links_active {
    opacity: 1;
    pointer-events: auto;
    cursor: default;
  }
}

@media screen and (max-width: 300px) {
  .burger_nav_active {
    width: 100%;
  }
}

@media screen and (max-width: 420px) {
